.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.contact-page {
  padding: 20px;
  font-family: "Roboto", sans-serif;
  line-height: 1.6;
}

.contact-page h1 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.contact-page p {
  text-align: center;
  font-size: 1.2rem;
  margin-bottom: 20px;
}

/* Form Styling */
.contact-form {
  max-width: 600px;
  margin: 0 auto 40px;
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.form-group textarea {
  height: 100px;
  resize: none;
}

.submit-btn {
  background: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
}

.submit-btn:hover {
  background: #0056b3;
}

.success-message {
  text-align: center;
  color: green;
  font-weight: bold;
  margin-top: 20px;
}

.contact-details,
.social-links {
  text-align: center;
  margin-top: 20px;
}

.social-links a {
  margin: 0 10px;
  text-decoration: none;
  color: #007bff;
}

.social-links a:hover {
  color: #0056b3;
}

.contact-page {
  padding: 40px;
  font-family: "Arial", sans-serif;
}

.contact-page h1 {
  text-align: center;
  margin-bottom: 30px;
  font-size: 2.5rem;
  color: #333;
}

.contact-container {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  align-items: flex-start;
  flex-wrap: wrap;
}

.contact-form {
  flex: 1;
  max-width: 600px;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.contact-form .form-group {
  margin-bottom: 20px;
}

.contact-form label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #333;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

.contact-form textarea {
  min-height: 100px;
  resize: vertical;
}

.contact-form .submit-btn {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.contact-form .submit-btn:hover {
  background-color: #0056b3;
}

.contact-details {
  flex: 1;
  max-width: 400px;
  background-color: #2c3e50;
  color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  align-items: center;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
}

.contact-details h2 {
  margin-bottom: 80px;
  color: #e67e22;
}

.contact-details p {
  margin-bottom: 20px;
  font-size: 1rem;
  color: #ccc;
}

.contact-details .footer-social {
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    align-items: center;
  }

  .contact-form,
  .contact-details {
    max-width: 100%;
  }
}
