body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f7f7f7;
}

.navbar {
  background-color: #333;
  color: white;
  padding: 20px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar .logo h2 {
  font-size: 24px;
  color: #fff;
  margin: 0;
  font-weight: 600;
  text-transform: uppercase;
}

.navbar .nav-links {
  display: flex;
  gap: 20px;
}

.navbar .nav-links a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.navbar .nav-links a:hover {
  color: #ff6f61;
  text-decoration: underline;
}

.hero {
  position: relative;
  text-align: center;
  margin-bottom: 30px;
}

.hero img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.hero-content {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero h1 {
  font-size: 48px;
  margin-bottom: 20px;
}

.search-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.search-input {
  padding: 10px;
  font-size: 16px;
  width: 200px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.search-button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 8px;
}

.search-button:hover {
  background-color: #0056b3;
}

.event-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin: 20px;
}

.event-card {
  border: 1px solid #ddd;
  padding: 15px;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.event-card .event-image {
  width: 100%;
  border-radius: 8px;
}

.event-card .event-details {
  margin-top: 15px;
}

.event-card h3 {
  font-size: 22px;
  font-weight: bold;
}

.event-location,
.event-date {
  font-size: 16px;
  color: #555;
}

.event-description {
  font-size: 14px;
  color: #777;
  margin-top: 10px;
}

.learn-more-button {
  background-color: #28a745;
  color: white;
  padding: 10px;
  text-decoration: none;
  border-radius: 4px;
  margin-top: 10px;
  display: inline-block;
}

.learn-more-button:hover {
  background-color: #218838;
}

.cta-container {
  text-align: center;
  margin-top: 40px;
}

.cta-button {
  background-color: #ff6f61;
  color: white;
  padding: 15px 30px;
  font-size: 18px;
  border: none;
  cursor: pointer;
  border-radius: 8px;
}

.cta-button:hover {
  background-color: #e04e44;
}

.highlight-location {
  font-weight: bold;
  color: #4caf50;
}

.highlight-date {
  font-weight: bold;
  color: #f44336;
}

