body {
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

nav,
.nav-links {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

p,
input,
button {
  font-family: "Open Sans", sans-serif;
}

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 400px;
  margin: 0 auto;
  padding: 30px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.form-title {
  font-size: 24px;
  font-weight: bold;
  color: #2c3e50;
  margin-bottom: 20px;
  text-align: center;
}

.form-input {
  width: 100%;
  padding: 10px 15px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: #f9f9f9;
  color: #2c3e50;
  transition: all 0.3s ease;
}

.form-input:focus {
  outline: none;
  border-color: #4caf50;
  box-shadow: 0 0 5px rgba(76, 175, 80, 0.5);
}

.form-button {
  width: 100%;
  padding: 12px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease;
}

.form-button:hover {
  background-color: #45a049;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.form-text {
  font-size: 14px;
  color: #777;
  margin-top: 10px;
  text-align: center;
}

.form-text a {
  color: #4caf50;
  text-decoration: none;
}

.form-text a:hover {
  text-decoration: underline;
}

.social-login {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.social-login a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #f5f5f5;
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.social-login a:hover {
  transform: scale(1.1);
}

.social-login .facebook {
  color: #3b5998;
}

.social-login .google {
  color: #db4437;
}

.social-login .linkedin {
  color: #0077b5;
}

.contact-page {
  font-family: "Roboto", Arial, sans-serif;
  margin: 0 auto;
  padding: 20px;
  max-width: 800px;
  color: #333;
}

.contact-page h1 {
  font-size: 2.5rem;
  color: #007bff;
  text-align: center;
  margin-bottom: 10px;
}

.contact-page p {
  font-size: 1rem;
  color: #666;
  text-align: center;
  margin-bottom: 20px;
}

.contact-form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-form .form-group {
  margin-bottom: 15px;
}

.contact-form .form-group label {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 5px;
  display: flex;
  text-align: left;
}

.contact-form .form-group input,
.contact-form .form-group textarea {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

.contact-form .form-group input:focus,
.contact-form .form-group textarea:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
}

.contact-form .form-group textarea {
  resize: none;
}

.contact-form .submit-btn {
  width: 100%;
  padding: 10px 15px;
  font-size: 1.2rem;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.contact-form .submit-btn:hover {
  background-color: #0056b3;
}

.success-message {
  color: green;
  font-size: 1rem;
  margin-top: 10px;
  text-align: center;
}

.contact-details {
  margin-top: 30px;
  padding: 20px;
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-details h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.contact-details p {
  font-size: 1rem;
  margin-bottom: 10px;
}

.contact-details a {
  color: #007bff;
  text-decoration: none;
  transition: all 0.3s ease;
}

.contact-details a:hover {
  text-decoration: underline;
}

.social-links h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  text-align: center;
}

.social-links a {
  margin: 0 10px;
  font-size: 1.2rem;
  color: #007bff;
  text-decoration: none;
  transition: all 0.3s ease;
}

.social-links a:hover {
  color: #0056b3;
}

@media (max-width: 768px) {
  .contact-page {
    padding: 15px;
  }

  .contact-details,
  .contact-form {
    padding: 15px;
  }

  .contact-form .submit-btn {
    font-size: 1rem;
  }
}

.footer {
  background-color: #2c3e50;
  color: white;
  padding: 20px 0;
