.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  gap: 20px;
  justify-content: center;
  padding: 20px 0;
}

.team-card {
  position: relative;
  width: 100%;
  aspect-ratio: 1 / 1.2;
  perspective: 1000px;
}

.team-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.8s ease;
}

.team-card:hover .team-card-inner {
  transform: rotateY(180deg);
}

.team-card-front {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.team-card-front img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.team-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background-color: #2e8b57;
  color: white;
  border-radius: 15px;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 10px;
}

.team-card-back h3 {
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.team-card-back p {
  font-size: 1.2rem;
  margin: 0;
}

.hero-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
}

.hero-content h1 {
  font-size: 4rem;
  font-weight: bold;
  margin: 0;
  letter-spacing: 3px;
  line-height: 1.2;
  animation: fadeIn 1.5s ease-in-out;
}

.hero-content h1 span {
  display: inline-block;
  background: linear-gradient(to right, #2ecc71, #27ae60);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textGlow 2s infinite;
}

.hero-content p {
  font-size: 1.5rem;
  margin-top: 20px;
  animation: slideUp 1s ease-in-out 1.5s forwards;
  opacity: 0;
}

.cta-btn {
  display: inline-block;
  margin-top: 30px;
  padding: 15px 30px;
  background: linear-gradient(to right, #16a085, #1abc9c);
  color: white;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, background 0.3s ease;
  animation: zoomIn 1.5s ease-out 2s forwards;
  opacity: 0;
}

.cta-btn:hover {
  background: linear-gradient(to right, #1abc9c, #16a085);
  transform: scale(1.1);
}

/* Animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes textGlow {
  0%,
  100% {
    filter: brightness(1);
  }
  50% {
    filter: brightness(1.5);
  }
}

@keyframes zoomIn {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

